import { Command } from 'commander';
import * as main from './main';
import { Utilities } from './utilities';

/* global process */

const program = new Command();

program
    .name('office-addin-manifest-converter')
    .description('CLI to the Office add-in manifest converter, XML to JSON')
    .usage('Usage: convert input-xml-manifest-path [--output <output-json-manifest-directory>] [-i] [-v]');

program
    .command('convert')
    .argument('<xml-manifest-file>')
    .combineFlagAndOptionalValue(false)  // boolean flags can be combined together, such as '-ivd'
    .option('-o, --output <directory>', 'path to the directory where the JSON manifest files will be written to')
    .option('-i, --image-download', 'the conversion should download the images into the output directory')
    .option('-u, --image-urls', 'insert urls instead of package relative references for images that come from inside of the extension element.')
    .option('-v, --verbose', 'extra log messages should be written to console')
    .option('-d, --debug', 'show asserts and other debugging information')
    .action(async (xmlManifestFile: string, options: any): Promise<void> => {
        await convert(xmlManifestFile, options);
    });

program.parse();


async function convert(xmlManifestFile: string, options: any) {
    try {
        const output: string = options.output;
        const imageDownload: boolean = Utilities.isNullOrUndefined(options.imageDownload) ? false : options.imageDownload;
        const imageUrls: boolean = Utilities.isNullOrUndefined(options.imageUrls) ? false : options.imageUrls;
        const verbose: boolean = Utilities.isNullOrUndefined(options.verbose) ? false : options.verbose;
        const dbg: boolean = Utilities.isNullOrUndefined(options.debug) ? false : options.debug;

        Utilities.setShowDebugInfo(dbg);
        Utilities.logDebug('Command line: convert ' + xmlManifestFile + ' ' + JSON.stringify(options));

        await main.convert(xmlManifestFile, output, imageDownload, imageUrls, verbose)
            .catch((e) => {
                Utilities.logError(e);
        });

        Utilities.log('Convert is complete');
    }
    catch (err) {
        Utilities.logError(err);
    }
}
